.materials {
    padding-top: 10px;
    padding-bottom: 43px;
}

.materials__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    font-size: 30px;
    color: #000000;
    font-family: "TTFirsNeue-Bold";
    line-height: 1.4;
    letter-spacing: 0.02em;
    text-align: center;
}

.materials__list {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    list-style: none;
}

.materials__image {
    margin-bottom: 12px;
}

.materials__lead {
    padding: 0;
    margin: 0;
    margin-bottom: 8px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";
    font-size: 18px;
}

.materials__text {
    padding: 0;
    padding-bottom: 23px;
    margin: 0;
    font-size: 15px;
}

@media (min-width: 576px) {
    .materials {
        padding-top: 28px;
        padding-bottom: 58px;
    }

    .materials__title {
        font-size: 36px;
        margin-bottom: 50px;
    }

    .materials__image {
        margin-bottom: 2px;
    }

    .materials__list {
        margin-bottom: 28px;
    }

    .materials__item {
        margin-bottom: 20px;
    }

    .materials .form__text-agree {
        display: none;
    }

    .materials .form__send {
        grid-column: 1/3;
    }
}    

@media (min-width: $tablet-width) {  
    .materials {
        padding-top: 47px;
        padding-bottom: 70px;
    }  

    .materials__title {
        text-align: left;
    }

    .materials__image {
        width: 170px;
        height: 170px;
        margin-left: -10px;
        margin-top: -10px;
    }

    .materials__list {
        margin-bottom: 35px;
    }

    .materials__item {
        display: grid;
        grid-template-columns: 170px 1fr;
        column-gap: 10px;
        align-items: center;
        padding-right: 7px;
    }

    
    .materials__lead {
        font-size: 20px;
    }

    .materials__text {
        font-size: 16px;
    }

    .materials__form.form {
        padding-top: 35px;
    }

    .materials .form__main {
       grid-template-columns: repeat(3, 1fr);
    }

    .materials .form__send {
        grid-column: 3/4;
    }

    .materials__text-wrap {
        padding-top: 7px;
    }
}    

@media (min-width: 992px) {
    .materials {
        padding-top: 60px;
        padding-bottom: 90px;
    }

    .materials__image {
        width: 180px;
        height: 180px;
        margin-left: -12px;
        margin-top: -8px;
        margin-bottom: 0;
    }

    .materials__title {
        font-size: 40px;
        margin-bottom: 60px;
    }

    .materials__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
        margin-bottom: 60px;
    }

    .materials__item {
        display: block;
        margin-bottom: 0;
    }

    .materials__text {
        padding-bottom: 0;
    }

    .materials__text-wrap {
        padding-top: 0;
    }
}    

@media (min-width: $desktop-width) {
    .materials {
        padding-top: 110px;
    }

    .materials__title {
        margin-bottom: 89px;
    }

    .materials__list {
        margin-bottom: 87px;
    }

    .materials__item {
        display: grid;
        grid-template-columns: 200px 1fr;
        column-gap: 16px;
        margin-bottom: 0;
        padding-right: 0;
        font-size: 16px;
        line-height: 1.5;
        font-family: "TTFirsNeue-regular";
    }

    .materials__image {
        width: 200px;
        height: 200px;
        margin-left: 0;
        margin-top: 0;
    }   
    
    .materials__text-wrap {
        margin-bottom: 3px;
    }

    .materials__form.form {
        display: grid;
        grid-template-columns: 310px 1fr;
        padding: 40px;
        column-gap: 40px;
    }

}    